body {
  margin: 0;
  height: 100;
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  
}


/* Avoid Chrome to see Safari hack */
@supports (-webkit-touch-callout: none) {
  body {
    /* The hack for Safari */
    height: -webkit-fill-available;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
